header {
	.row {
		height     : 110px;
		align-items: center;
		padding    : 18px 0;

		@media (max-width: 1199.98px) {
			height: 85px;
		}

		@media (max-width: 768px) {
			height: 60px;
		}
	}

	.logo {
		position: relative;
		height  : 24px;

		a {
			height : 100%;
			display: flex;
		}

		.white-logo {
			display: none;
		}

		svg,
		img {
			height: 100%;
			width : auto;
		}

		@media (max-width: 1024px) {
			height: 18px;
		}
	}

	.main-nav {
		margin-left: auto;
	}

	.header-nav {
		list-style-type: none;
		padding-left   : 0;
		display        : flex;

		.header-nav-item {
			a {
				font-size     : 18px;
				color         : #1c1c1c;
				text-transform: uppercase;
				transition    : all .2s ease-in-out;
				border-bottom : 2px solid transparent;
			}

			&.active,
			&.default,
			&:hover {
				a {
					border-bottom: 2px solid #3462ab;
					color        : #3462ab;
				}
			}

			+.header-nav-item {
				margin-left: 50px;
			}

			@media (max-width: 1024px) {
				a {
					font-size: 14px;
				}

				+.header-nav-item {
					margin-left: 20px;
				}
			}
		}
	}

	&.new-header {
		background: #002d56;

		.logo {
			height: 35px;

			.blue-logo {
				display: none;
			}

			.white-logo {
				display: block;
			}

			@media (max-width: 1024px) {
				height: 18px;
			}
		}

		.header-nav {
			a {
				color: #ffffff;
			}
		}
	}
}